@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #ffffff;
  --primary-light-gray: #f4f4f4;
  --primary-medium-gray: #d4d4d4;
  --primary-gray: #888888;
  --primary-dark: #343a40;
  --primary-black: #000000;
  --primary-darkSE: rgb(52, 58, 64);
  --primary-transparent-black: rgba(0, 0, 0, 0.8);
  --primary-green: #009e15;
  --primary-red: #b90000;
  --primary-blue: #224693;
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}

/************************ PARTICLES *********************************/

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

.about-heading {
  color: var(--primary-dark);
  text-transform: uppercase;
  font-weight: 525;
  display: flex;
  justify-content: center;
  padding-bottom: 3rem;
  padding-top: 3rem;
  margin: 0;
}

.aboutMe .row {
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
}

.about-photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 20rem;
  border-radius: 50%;
}

.aboutMe p {
  margin-bottom: 0;
}

@media (max-width: 991px) {
  .about-heading {
    font-size: 2rem !important;
    padding-bottom: 2rem;
    padding-top: 2rem;
  }

  .aboutMe {
    padding-left: 2rem !important;
    padding-right: 2rem !important;
  }

  .aboutMe .row {
    margin-bottom: 2rem;
  }

  .about-photo-wrap {
    margin-bottom: 2rem;
  }

  .profile-img {
    width: 12.5rem;
  }
}

.contacts {
  background: var(--primary-light-gray);
}

.contacts h1 {
  color: var(--primary-dark);
  padding: 3rem 0 3rem 0;
  text-transform: uppercase;
  font-weight: 525;
  margin-bottom: 0;
}

.contacts p {
  color: var(--primary-dark);
  font-size: 1.2rem;
  margin-bottom: 0;
  padding-bottom: 1rem !important;
}

.contacts .row {
  margin-bottom: 1rem;
}

.contacts input,
.contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid var(--primary-dark);
  border-radius: 0;
  color: var(--primary-dark);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts input:focus,
.contacts textarea:focus {
  background: transparent;
  color: var(--primary-gray);
  border-bottom: 0.125rem solid var(--primary-dark);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contacts .btn-main-offer {
  width: 100%;
  background: var(--primary-dark);
  margin: 0;
  margin-bottom: 3rem;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.15rem;
  background: var(--primary-blue);
  display: inline-block;
}

input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.4s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: var(--primary-red);
  background: transparent;
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: var(--primary-green);
}

@media (max-width: 991px) {
  .contacts {
    overflow-x: hidden;
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .contacts h1 {
    font-size: 1.7rem;
    padding: 2rem 0 2rem 0;
  }

  .contacts p {
    padding-bottom: 0.5rem;
  }

  .contacts .container {
    padding: 0;
  }
}

.education {
  background-color: var(--primary-light-gray);
  padding-bottom: 3rem;
}

.education p {
  margin-bottom: 0;
}

.education-wrapper {
  position: relative;
  overflow: hidden;
}

.education h1 {
  text-transform: uppercase;
  color: var(--primary-dark);
  font-weight: 525;
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin-bottom: 0;
}

.education-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: var(--primary-dark);
  z-index: 1;
}

.marker {
  width: 1rem;
  height: 1rem;
  align-self: center;
  border-radius: 50%;
  border: 0.325rem solid var(--primary-dark);
  background: var(--primary-dark);
  margin-top: 0.5rem;
  z-index: 3;
}

.timeline-block:hover .marker {
  -webkit-transform: scale(2, 2);
          transform: scale(2, 2);
  background: var(--primary-blue);
  border: 0.325rem solid var(--primary-blue);
  transition: 0.4s ease-in-out;
}

.timeline-block-left:hover .timeline-content {
  padding-right: 2rem;
  transition: 0.4s ease-in-out;
}

.timeline-block-right:hover .timeline-content {
  padding-left: 2rem;
  transition: 0.4s ease-in-out;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
  margin-bottom: 1rem;
}

.timeline-block:hover .timeline-content h3 {
  font-weight: bold;
  transition: 0.4s ease-in-out;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  text-align: right;
  float: left;
  direction: rtl;
}

.timeline-content {
  padding: 0 1rem 0 1rem;
  width: 95%;
  color: var(--primary-dark);
  direction: ltr;
}

.timeline-content h3 {
  color: var(--primary-dark);
}

@media (max-width: 991px) {
  .marker {
    margin-top: 0.35rem;
    left: 0;
  }

  .education {
    padding-bottom: 2rem;
    padding-right: 1rem;
    padding-left: 0.5rem;
  }

  .education h1 {
    font-size: 2rem !important;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }

  .education-wrapper::before {
    left: 0;
    margin-left: 1.38rem;
  }

  .timeline-content {
    padding-left: 0.5rem;
  }

  .timeline-content h3 {
    font-size: 1.5rem !important;
  }

  .timeline-block-right {
    float: none;
    width: 100%;
  }

  .timeline-block-left {
    text-align: left;
    float: none;
    direction: ltr;
    width: 100%;
  }

  .timeline-block-left:hover .timeline-content {
    padding-left: 2rem;
    transition: 0.4s ease-in-out;
  }
}

.experience {
  padding-bottom: 3rem;
}

.experience p {
  margin-bottom: 0;
}

.experience-wrapper {
