.skills {
  text-align: center;
  background-color: var(--primary-light-gray);
}

.skills h1 {
  color: var(--primary-dark);
  text-transform: uppercase;
  font-weight: 525;
  margin: 0;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.skills h3 {
  color: var(--primary-dark);
}

.skills .container {
  padding-bottom: 1.5rem;
}

.skills .circle {
  position: relative;
  margin: 0.375rem auto;
  background: var(--primary-dark);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.skills .icon {
  font-size: 1.4rem;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.skills .box {
  height: 16rem;
  border-bottom: 0.3125rem solid var(--primary-gray);
  padding: 0.5rem;
  background: var(--primary-white);
  transition: 0.4s ease-in-out;
  margin-bottom: 1.5rem;
}

.skills .box:hover {
  background: var(--primary-gray);
  border-bottom: 0.3125rem solid var(--primary-blue);
  color: var(--primary-white);
  transition: 0.4s ease-in-out;
}

.skills .box:hover .circle {
  background: var(--primary-blue);
  transition: 0.4s ease-in-out;
}

.skills .box:hover h3 {
  color: var(--primary-white);
  transition: 0.4s ease-in-out;
}

@media (max-width: 991px) {
  .skills .container {
    padding-bottom: 1rem;
    padding-right: 2rem;
    padding-left: 2rem;
  }

  .skills .box {
    margin-bottom: 2rem;
  }

  .skills h1 {
    font-size: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
}

